import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import WebService from '../../../ultities/WebServices';
import HomeSlide from './HomeSlide'
import './HomePage.css'
import homepage from '../../../assets/images/about.png'
import learn1 from '../../../assets/images/learn.png'
import learn2 from '../../../assets/images/learn1.png'
import learn3 from '../../../assets/images/learn2.png'

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.webService = new WebService();
  }
  componentWillMount() {
    if (this.webService.loggedIn() && this.webService.isAdmin()) {
      this.props.history.replace('/admin');
    }
  }
  render() {
    return (
      <div className="home-page">
        <HomeSlide />
        <HomeBody1 />
        <HomeBody2 />
      </div>
    );
  }
}

class HomeBody1 extends Component {
  render() {
    return (
      <div className="homebody1">
        <div className="container">
          <div className="row row0">
            <div className="text-col1-custom">
              <h2>Cho mng bn!</h2>
              <p>Hy ng hnh cng chng ti  vt qua k thi tuyn vo lp 10 mn Ting Anh vi kh nng ca chng ti.</p>
              <div className="row-intro">
                <div className="row icon">
                  <div className="ico-homepage">
                    <i className="fas fa-graduation-cap fa-4x fas-custom"></i>
                  </div>
                  <div className="text-homepage">
                    <h3>Luyn thi online</h3>
                    <p>n tp kin thc, luyn cc  thi chy lng v  thi ca cc nm. Theo sau  l kt qu ging gii sinh ng.</p>
                  </div>
                </div>
                <div className="row icon">
                  <div className="ico-homepage">
                    <i className="fas fa-chalkboard-teacher fa-4x fas-custom"></i>
                  </div>
                  <div className="text-homepage">
                    <h3>Hc t gio vin</h3>
                    <p>c hng dn chi tit v kin thc mt cch d hiu trc quan ca nhng giao vin gii nhiu nm kinh nghim trong ngh gio.</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="img-homepage">
              <img className="img-custom" src={homepage} alt=""></img>
            </div>
          </div>
          <hr></hr>
        </div>
      </div>
    );
  }
}

class HomeBody2 extends Component {
  render() {
    return (
      <div className="homebody2">
        <div className="title-content container">
          <h2>4 bc hc hiu qu</h2>
          <p>Thc hnh luyn thi online vo lp 10 mn ting anh hiu qa vi 4 bc</p>
        </div>
        <div className="row row1">
          <div className="col-sm-4 col-md-4">
            <img className="img-learn" src={learn1} alt=""></img>
            <h3>n luyn kin thc</h3>
            <p>Thc hnh, luyn tp kin thc c bn, nm kin thc</p>
          </div>
          <div className="col-sm-4 col-md-4">
            <img className="img-learn" src={learn2} alt=""></img>
            <h3>Luyn b  thi</h3>
            <p>Luyn gio trnh luyn thi chuyn nghip, t tiu chun quc gia</p>
          </div>
          <div className="col-sm-4 col-md-4">
            <img className="img-learn" src={learn3} alt=""></img>
            <h3>Luyn  thi cc nm</h3>
            <p>Luyn  thi ca cc nm, a ra nh gi kh quan v nng lc</p>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(HomePage);



