export default class SystemHelper {
  checkValidaRegister(e) {
    let error = false;
    let errMess = '';
    const dob = new Date(e.target.dob.value).toDateString()
    console.log(dob)
    if (error === false && !this.checkNumAndChar(e.target.username.value)) {
      error = true;
      errMess = '"Ti khon" khng c trng hoc cha k t c bit';
    }
    if (error === false && !this.checkNumAndChar(e.target.password.value)) {
      error = true;
      errMess = '"Mt khu" khng c trng hoc cha k t c bit';
    }
    if (error === false && !this.checkSpeclChar(e.target.name.value)) {
      error = true;
      errMess = '"Tn" khng c trng hoc cha k t c bit';
    }
    if (error === false && dob === 'Invalid Date') {
      error = true;
      errMess = '"Ngy sinh" khng ng';
    }
    if (error === false && !this.checkNumAndChar(e.target.sex.value)) {
      error = true;
      errMess = '"Gii tnh" khng c trng hoc cha k t c bit';
    }
    if (error === false && this.checkEmty(e.target.address.value)) {
      error = true;
      errMess = '"a ch" khng c trng';
    }
    if (error === false && !this.checkSpeclChar(e.target.school.value)) {
      error = true;
      errMess = '"Trng hc" khng c trng hoc cha k t c bit';
    }
    return ({
      err: error,
      errMess: errMess
    })
  }
  checkEmty(value) {
    if (value == null || value === '') {
      return true;
    }
    return false;
  }
  checkNumAndChar(value) {
    if (value.match(/^[0-9a-zA-Z]+$/)) {
      return true;
    }
    return false;
  }
  checkSpeclChar(value) {
    // var format = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/;
    var format = /[!@#$%^&*()_+\-=[\]{};':"\\|,.<>/?]+/;
    if (this.checkEmty(value)){
      return false
    }
    if (format.test(value)) {
      return false;
    } else {
      return true;
    }
  }

  chuyenDoiURL(str) {
    // Chuyn ht sang ch thng
    str = str.toLowerCase();

    // xa du
    str = str.replace(/(||||||||||||||||)/g, 'a');
    str = str.replace(/(||||||||||)/g, 'e');
    str = str.replace(/(||||)/g, 'i');
    str = str.replace(/(||||||||||||||||)/g, 'o');
    str = str.replace(/(||||||||||)/g, 'u');
    str = str.replace(/(||||)/g, 'y');
    str = str.replace(/()/g, 'd');

    // Xa k t c bit
    str = str.replace(/([^0-9a-z-\s])/g, '');

    // Xa khong trng thay bng k t -
    str = str.replace(/(\s+)/g, '-');

    // xa phn d -  u
    str = str.replace(/^-+/g, '');

    // xa phn d -  cui
    str = str.replace(/-+$/g, '');

    // return
    return str;
  }

}


